import Block4 from './block4';
import { connect } from 'react-redux'
import { updateInputTextAC } from '../../../Redux/registerReducer';


let mapStateToProps = (state) => {
    return{
        newPostText: state.registerPage.newPostText
    }
}
let mapDispatchToProps = (dispatch) => {
    return{
        updateInputText: (text) => {
            let action = updateInputTextAC(text);
            dispatch(action);
        }
    }
}

const Block4Container = connect(mapStateToProps, mapDispatchToProps)(Block4);
export default Block4Container;