import React, { Component } from 'react';
import C from './block4.module.css'
import Inputs from './inputRegister';


export default class Block4 extends React.Component{

   
    constructor() {
        super();
        this.state = {
            error: null,
            isLoaded: false,
            items: [],
        }
   
    }
    componentDidMount() {
        fetch("https://frontend-test-assignment-api.abz.agency/api/v1/positions")
            .then(res => res.json())
            .then(
                (result) => {
                    this.setState({
                        isLoaded: true,
                        items: result.positions
                    })
                },
                (error) => {
                    this.setState({
                        isLoaded: true,
                        error
                    })
                }
            )
    }

    render() {
   
        const { error, items, isLoaded } = this.state;
        if (error) {
            return <p>Error {error.message}</p>
        } else if (!isLoaded) {
            return <p>Loading...</p>
        }
        else {
            return (
                <div className={C.block4} id='box'>
                    <div className={C.desc_wrap}>
                        <h1>Register to get a work</h1>
                        <p>Attention! After successful registration and alert, update the <br />
                    list of isers in the block from the top</p>
                    </div>
                <Inputs />
                    <div className={C.radio_p_wrap}>
                        <p>Select your position</p>
                        {items.map(item => (
                            <div className={C.radio_wrap}>
                                <input type="radio" name="position" /> <div>{item.name}</div>
                            </div>
                        ))}
                        <div className={C.upload_photo_menu}>
                            <p>Photo</p>
                            <div className={C.load_photo}>
                                <input type="text" className={C.upload} placeholder="  Upload your photo" />

                                <button className={C.upload}>Browse</button>
                            </div>
                        </div>
                        <div className={C.button_wrap}>   <button className={C.sign}>Sign up now</button> </div>
                    </div>


                </div>
            )
        }
    }
}
