import React, { Component } from 'react';
import C from './block3.module.css'
import Ava from '../../assets/photo-cover.png'

class Block3 extends Component {
    constructor(props) {
        super();
        this.state = {
            error: null,
            isLoaded: false,
            items: []
        }
    }

    componentDidMount() {
        fetch("https://frontend-test-assignment-api.abz.agency/api/v1/users?page=1&count=6")
            .then(res => res.json())
            .then(
                (result) => {
                    this.setState({
                        isLoaded: true,
                        items: result.users
                    });
                },
                (error) => {
                    this.setState({
                        isLoaded: true,
                        error
                    })
                }

            )

    }
    render() {
        const { error, isLoaded, items } = this.state;
        if (error) {
            return <p> Error {error.message}</p>

        }
        else if (!isLoaded) {
            return <p>Loading</p>
        } else {
            return (
                <div className={C.block3}>
                    <h1>Our cheerful users</h1>
                    <p>Attention! Sorting users by registration date</p>
<div className={C.user_wrap}>
                    {items.map(item => (

                            <div className={C.human_wrap}>
                                <img src={item.photo = true ? item.photo : Ava} alt="" />
                                <h2>{item.name}</h2>
                                <p>{item.position}</p>
                                <p>{item.email}</p>
                                <p>{item.phone}</p>
                            </div>

                            ))}
                            </div>
                            <div className={C.button_wrap}>   <button className={C.sign}>Show nore</button> </div>
                       
                </div>
            )
        }
    }
}
export default Block3;