    render() {
    
        
        let newInputElement = React.createRef();

        let onInputChange = () => {
            let text = newInputElement.current.value;
            this.props.updateInputText(text);
        }
        return (
            <div className={C.input_wrap}>

                <p>Name</p>
                <input type="text" placeholder="  Your name"
                    ref={newInputElement}
                    onChange={onInputChange}
                    value={this.props.newPostText}
                />
                <p>Email</p>
                <input type="text" placeholder="  Your email"
                    ref={newInputElement}
                    onChange={onInputChange}
                    value={this.props.newPostText}
                />
                <p>Phone number</p>
                <input type="text" placeholder="  +380 XX XXX XX XX"
                    ref={newInputElement}
                    onChange={onInputChange}
                    value={this.props.newPostText}
                />

            </div>
        )
    }
